.form {
	&__item {
		position: relative;
		display: flex;
		align-items: center;
		min-height: 52rem;
		margin-bottom: 24rem;
		background: #FBFBFB;
		box-shadow: 5rem 5rem 25rem rgba(153, 173, 187, 0.1);
		border-radius: 5rem;

		.nice-select {
			width: 100%;
			padding-top: 5rem;
			padding-bottom: 5rem;
			height: 100%;
			padding-left: 24rem;

			&::after {
				right: 27rem;
			}
		}
	}

	&__icon {
		position: absolute;
		top: 50%;
		left: 24rem;
		display: block;
		width: 24rem;
		height: 24rem;
		pointer-events: none;
		transform: translateY(-50%);

		svg {
			width: 100%;
			height: 100%;
		}
	}

	.input {
		position: relative;
		width: 100%;
		display: block;
		padding: 14rem 29rem 14rem 69rem;
		border: 1rem solid transparent;
		background: none;
		outline: none;
		font-size: 16rem;
		line-height: 150%;
		border-radius: 5rem;
		overflow: hidden;
		z-index: 1;
		transition: 300ms;

		&:focus {
			border-color: #7C9DA8;
		}

		&.invalid {
			border-color: #F96190;
		}
	}

	.placeholder {
		position: absolute;
		top: 50%;
		left: 67rem;
		display: block;
		padding: 1rem 5rem;
		font-size: 16rem;
		line-height: 150%;
		color: #999;
		transform: translateY(-50%);
		pointer-events: none;
		background: #FBFBFB;
		border-radius: 5rem;
		transition: 300ms;
		z-index: 0;

		&.active {
			top: 0;
			font-size: 14rem;
			z-index: 2;
		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 40rem;
	}

	&__link {
		font-size: 16rem;
		line-height: 150%;

		>span {
			color: #999;
		}

		a {
			span {
				padding: 0;

				&::after {
					bottom: 3rem;
				}

				&::before {
					content: none;
				}
			}
		}
	}

	.checkbox {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 20rem;
		height: 20rem;
		border-radius: 50%;
		border: 1.5rem solid #181818;

		&::after {
			content: "";
			position: absolute;
			top: 5rem;
			left: 5rem;
			width: 8rem;
			height: 5rem;
			border: 1.5rem solid #181818;
			border-top: none;
			border-right: none;
			transform: rotate(-45deg);
			opacity: 0;
			transition: 500ms;
		}

		&_text {
			>span {
				color: #7C9DA8;
			}
		}
	}

	&__checkbox {
		position: relative;
		max-width: 700rem;
		margin-bottom: 24rem;
		padding-left: 34rem;
		user-select: none;
		font-size: 16rem;
		line-height: 150%;

		label {
			cursor: pointer;
		}

		input:checked+.checkbox::after {
			opacity: 1;
		}
	}

	.files {
		+* {
			margin-top: 48rem;
		}

		+.files {
			margin-top: 24rem;
		}

		&__zone {
			position: relative;
			min-height: 128rem;
			margin-bottom: 16rem;
			padding: 20rem 20rem 20rem 128rem;
			font-size: 16rem;
			line-height: 150%;
			border: 2rem dashed #DEE4F3;
			border-radius: 5rem;
			background: url(../images/forms/jpeg.svg) no-repeat 24rem 24rem / 80rem;
			transition: 500ms;
		}

		&__title {
			margin-bottom: 8rem;
		}

		&__descr {
			max-width: 440rem;
			color: #999;
		}

		&__btn {
			position: absolute;
			top: 38rem;
			right: 24rem;
			min-width: 140rem;
			padding: 13rem 20rem;
			font-weight: 700;
			font-size: 16rem;
			line-height: 150%;
			border: 1rem solid #BBCBD0;
			box-shadow: 5rem 5rem 25rem rgba(153, 173, 187, 0.1);
			border-radius: 5px;
			background: none;
		}
	}

	.file {
		&__progress {
			display: flex;
			align-items: center;
			min-height: 52rem;
			padding: 13rem 24rem;
			font-size: 16rem;
			line-height: 150%;
			background: rgba(222, 228, 243, 0.5);
			border: 1rem solid #DEE4F3;
			border-radius: 5rem;

			&:not(:last-child) {
				margin-bottom: 20rem;
			}
		}

		&__name {
			margin-right: 16rem;
		}

		&__weight {
			color: #999;
		}

		&__icon {
			margin-left: auto;
		}
	}

	.required {
		background: url(../images/icons/asterisk.svg) no-repeat center right 24rem / 15rem;
	}

	&_login {
		max-width: 1003rem;
	}

	&__agreement {
		max-width: 410rem;
		margin-top: 12rem;
		font-size: 16rem;
		line-height: 150%;

		span {
			color: #696D70;
		}

		a {
			padding: 0;
			font-weight: 700;

			&::before {
				content: none;
			}

			&::after {
				bottom: 1.5rem;
			}
		}
	}
}

.drop-zone--over {
	border-color: red !important;
}

.drop-zone__thumbs {
	display: flex;
	flex-wrap: wrap;
	grid-gap: 20rem;
	margin-top: 30rem;
}

.drop-zone__thumb {
	width: 200rem;
	height: 200rem;
	border-radius: 10rem;
	overflow: hidden;
	background-color: #cccccc;
	background-size: cover;
	background-position: center;
	position: relative;
}

.drop-zone__thumb::after {
	content: attr(data-label);
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	padding: 5rem 0;
	color: #ffffff;
	background: rgba(0, 0, 0, 0.75);
	font-size: 14rem;
	text-align: center;
}